import React, {Component} from 'react';
import './App.css';
import Main from './components/Layout/Main/Main.js'
import About from './components/Layout/About/About.js'
import Notfound from './components/Layout/Notfound/Notfound.js'
import ScrollToTop from './components/Hoc/ScrollToTop.js'
import {
    HashRouter,
    Route,
    Switch,

} from 'react-router-dom';


class App extends Component {
    render() {
        return <HashRouter>
            <ScrollToTop>
            <Switch>
                <Route exact path='/' component={Main}/>
                <Route path='/who' component={About}/>
                <Route component={Notfound}/>
            </Switch>
            </ScrollToTop>

        </HashRouter>;
    }
}


export default App;
